@use "global"as *;

// button
.c-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 24rem;
  height: 5rem;
  color: #fff;
  font-size: 1.6rem;
  background: $color_light-orange;
  border-radius: 2.5rem;
  position: relative;
  line-height: 1.25;
  font-weight: bold;

  &.-brown {
    background: $color_light-brown;
  }

  &.-white {
    color: $color_font;
    background: #fff;
  }

  &.-circle-right {
    &::after {
      content: '\f138';
      color: #fff;
      font-size: 1.8rem;
      right: 1.1rem;
      @include fontAwesome__sol;
      @include center(y);
    }
  }
}

// heading
.c-h2 {
  color: $color_dark-brown;
  font-weight: bold;
  @include fontsize(24, 28);
}

.c-h3 {
  color: $color_dark-brown;
  font-weight: bold;
  @include fontsize(18, 22);
}

// box
.c-white-box-s {
  background: #fff;
  border-radius: 1rem;

  @include media(tb) {
    border-radius: 3rem;
  }
}

.c-white-box-l {
  background: #fff;
  border-radius: 1rem;
  padding: 2.5rem;

  @include media(tb) {
    border-radius: 3rem;
    padding: 4.5rem 8rem 5rem;
  }

  &__h1 {
    font-weight: bold;
    color: $color_dark-brown;
    line-height: 1.56;
    text-align: center;
    margin-bottom: 2rem;
    @include fontsize(18, 28);

    @include media(tb) {
      margin-bottom: 3rem;
    }
  }
}

.c-small-box-list {
  display: flex;
  flex-direction: column;
  gap: 2rem 0;
  padding-bottom: 4rem;

  @include media(tb) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 4rem 4rem;
    padding-bottom: 6rem;
  }

  >* {
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 1rem;
    padding: 2.5rem 2.5rem 3rem;

    @include media(tb) {
      border-radius: 3rem;
      width: calc((100% - 4rem) / 2);
      padding: 3.5rem 4.5rem 3rem;
    }

    .c-btn {
      margin-top: auto;
    }
  }

  &__h1 {
    text-align: left;
    margin-bottom: 1.5rem;
    width: 100%;

    @include media(tb) {
      margin-bottom: 1rem;
    }
  }

  &__txt-box {
    margin-bottom: 2.5rem;

    @include media(tb) {
      margin-bottom: 5rem;
    }
  }
}